class LoggingPlugin {
  constructor(eventBus) {
    eventBus.on('shape.added', () => {
      console.log('A shape was added to the diagram!');
    });
  }
}

LoggingPlugin.$inject = [ 'eventBus' ];

export default {
  __init__: [ 'loggingPlugin' ],
  loggingPlugin: [ 'type', LoggingPlugin ]
};
